.w-e-block {
  margin-top: 4vw;
  margin-bottom: 0;
  list-style: none;
  padding-left: 0;

  &__item {
    flex-basis: 48%;
    margin-bottom: 3vw;
    padding: 1.2rem 1.8rem;
    background-color: $bg_1;
    border-radius: .375rem;

    &--size-l {
      flex-basis: 60%;
    }

    &--bg-1 {
      background-image: url('../images/ad_bg_1.png');
      background-position: 100% 0;
      background-size: 13rem;
      background-repeat: no-repeat;
    }

    &--bg-2 {
      background-image: url('../images/ad_bg_2.jpg');
      background-position: 100% -2.25rem;
      background-size: 8rem;
      background-repeat: no-repeat;
    }
  }

  @media screen and (max-width: $mobile - 1) {
    .btn {
      width: 100%;
    }
  }

  @media screen and (min-width: $tablet_s) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    &__item {
      margin: 0 1% 2%;
      padding: 2rem 2.5rem;
    }

    &--bg-1 {
      background-size: 10rem;
    }

    &--bg-2 {
      background-size: contain;
    }
  }

  @media screen and (min-width: $tablet) {
    .tab-flex {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
    }
  }
}

.mobile-wrap {
  display: flex;
  flex-direction: column-reverse;

  @media screen and (max-width: $tablet_s - 1) {
    .text-block {
      border-bottom: none;
      padding-bottom: 0;
    }
  }

  @media screen and (min-width: $tablet_s) {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.mobile {
  position: relative;
  min-width: 80%;
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
  border-radius: 50%;
  overflow: hidden;

  &__img {
    width: 100%;
  }

  @media screen and (min-width: $mobile) {
    min-width: 22rem;
    max-width: 22rem;
  }

  @media screen and (min-width: $tablet_s) {
    min-width: 50%;
    max-width: 50%;
    margin-right: initial;
  }

  @media screen and (min-width: $tablet) {
    &__img {

    }
  }

  @media screen and (min-width: $desktop) {
    &__img {

    }
  }
}

.btn-get-app {
  @media screen and (max-width: $mobile - 1) {
    width: 100%;
    display: flex;
    justify-content: center;

    > div > * {
      display: inline-block;
      margin-left: .4rem;
    }
  }
}
