.plans {
  margin: 0;
  padding-left: 0;
  padding-top: 6rem;
  list-style: none;

  &__item {
    background-color: $white;
    box-shadow: 0 0 25px -2px rgba(0, 0, 0, .15);
    margin-bottom: 3vw;
  }

  @media screen and (min-width: $tablet_s) {
    display: flex;
    justify-content: space-between;

    &__item {
      flex-basis: 33.333%;
      padding: 0 3rem;
      box-shadow: none;
      margin-bottom: 0;

      &:nth-child(2) {
        border-right: 1px solid $gray_1;
        border-left: 1px solid $gray_1;
      }
    }
  }

  @media screen and (min-width: $tablet) {
    &__item {
      padding: 0 4rem;
    }
  }
}

.p-content {
  display: none;
  padding-top: 1rem;

  &.visible {
    display: block;
  }
}

.plan {
  position: relative;
  padding: 1.2rem 1.2rem 1rem 5rem;

  .btn--s {
    float: right;
    margin-left: 1rem;
  }

  &__img {
    position: absolute;
    left: 0;
    width: 4.5rem;
  }

  &__price {
    display: flex;
    //margin-bottom: 1.2rem;
    margin-bottom: 0;

    i {
      align-self: flex-start;
      font-size: 1.125rem;
      font-weight: bold;
      font-style: normal;
    }

    p {
      margin: 0 .3rem;
      padding: 0;
      font-weight: bold;
      font-size: 1.5rem;
    }

    span {
      align-self: flex-end;
      opacity: .32;
      font-size: 1rem;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 2vw;
    font-size: 1.125rem;
    font-weight: bold;
  }

  &__p {
    position: relative;
    font-size: .875rem;

    & + .plan__p {
      margin-top: 1.2rem;
    }

    &::before {
      content: '';
      position: absolute;
      left: -1.5rem;
      top: .2rem;
      width: 1rem;
      height: .5rem;
      border-left: 2px solid $gray_1;
      border-bottom: 2px solid $gray_1;
      transform: rotateZ(-45deg);
    }
  }

  &__link {
    padding-top: 1.2rem;

    a {
      color: $acccent;
      text-decoration: none;
      transition: opacity .2s;

      &:hover {
        opacity: .7;
      }
    }
  }

  &__actions {
    padding: 3vw 0;
    text-align: center;
  }

  @media screen and (min-width: $tablet_s) {
    padding: 0;

    &__img {
      width: 6rem;
      position: static;
      margin: -3rem auto 3rem;
    }

    &__title {
      text-align: center;
    }

    .p-content {
      display: block;
    }

    .j-toggle-plans {
      display: none;
    }
  }
}

.coming-features {
  overflow: hidden;

  &__title {
    margin-bottom: 3rem;

    > * {
      text-align: left;
    }
  }

  &__tags {
    flex-basis: 50%;
    text-align: center;
  }

  @media screen and (min-width: $tablet) {
    display: flex;

    &__title {
      flex-basis: 50%;
      margin-bottom: 0;
    }
  }
}

.tag {
  display: inline-block;
  padding: 1.4rem 2.6rem;
  color: $acccent;
  font-weight: bold;
  background-color: $gray_3;
  border-radius: .375rem;

  &--1 {
    &::before {
      bottom: 1.8rem;
      right: -8rem;
      width: 2.5rem;
    }

    &::after {
      bottom: 0;
      right: -7rem;
      width: 5rem;
    }
  }

  &--2 {
    &::before {
      width: 2rem;
    }

    &::after {
      width: 5.8rem;
    }
  }

  &--lines {
    position: relative;

    &::before,
    &::after {
      content: '';
      position: absolute;
      height: 1rem;
      background-color: $gray_3;
      border-radius: .2rem;
    }
  }

  @media screen and (max-width: $mobile - 1) {
    &--2 {
      float: right;
      margin-top: 1rem;

      &::before {
        top: 2rem;
        left: -10rem;
      }

      &::after {
        top: 2rem;
        left: -7rem;
      }
    }

    &--3 {
      float: left;
      clear: both;
      margin-top: 1rem;
    }
  }

  @media screen and (min-width: $mobile) {
    &--2 {
      margin-top: 4.5rem;

      &::before {
        top: -2rem;
        left: -3rem;
      }

      &::after {
        top: -2rem;
        left: 0;
      }
    }

    &--3 {
      position: relative;
      top: -2.5rem;
      margin-left: 2rem;
    }
  }
}
