*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  font-family: $base-font;
  line-height: 1.4;

  //@media screen and (max-width: $tablet_s - 1) {
  //  font-size: 14px;
  //}

  @media screen and (min-width: $tablet_s) {
    font-size: 16px;
  }
}

body {
  color: $base-txt;
}

.clearfix::after {
  content: ".";
  display: block;
  clear: both;
  font-size: 0;
  height: 0;
  visibility: hidden;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  position: relative;
  max-width: 71.25rem;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1.5625rem;
}

.btn {
  padding: 1.35rem 2rem;
  color: $blue;
  background-color: $bg_btn;
  border-radius: .375rem;
  border: 1px solid transparent;
  cursor: pointer;
  font-size: 1rem;
  letter-spacing: .05rem;
  transition: background-color.2s ease-out, border-color.2s ease-out, color.2s ease-out;

  &--s {
    padding: .5rem .6rem;
    font-size: .875rem;
  }

  &--white {
    background-color: $bg_2;
  }

  &--c-gray {
    color: $gray_2;
  }

  &--left-icon {
    display: flex;
    align-items: center;
    padding: 1.1rem 1.6rem;

    > img {
      width: 1.4rem;
      margin-right: 1.4rem;
    }

    > span {
      text-align: left;
    }
  }

  &::-moz-focus-inner {
    border: 0;
  }

  &:focus {
    outline: none;
  }

  &:active {
    transform: scale(.99);
  }

  &:hover {
    background-color: transparent;
    color: $yellow;
    border-color: $yellow;
  }
}

.link {
  text-decoration: none;
  transition: color .3s;

  &:hover {
    color: $acccent;
  }
}

.hint {
  padding: 2rem 3rem;
  color: $blue;
  background-color: $bg_btn;
  border-radius: .4rem;
}
