
body {
  overflow-x: hidden;
}

.sub-nav {
  display: none;
  padding-left: 0;
  list-style: none;
  background-color: $white;
  transition: height .4s;

  &__item {
    display: block;
    padding: 1rem 1rem 1rem 2.5rem;
    color: $blue_dark;
    text-decoration: none;
    font-size: .875rem;
    cursor: pointer;
    transition: color .3s;

    &:hover {
      color: $blue;
    }

  }

  @media screen and (min-width: $desktop) {
    .divider {
      display: block;
    }

    &__item {
      display: inline-block;
      padding: .5rem 1.8rem;
    }
  }
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;

  > li {
    margin: 0;
    color: $blue_dark;
    transition: color .3s;

    &.has-sub-nav {
      .nav-list__item::after {
        content: '';
        position: absolute;
        right: 2rem;
        top: 1.5rem;
        display: inline-block;
        width: .5rem;
        height: .5rem;
        margin-left: 1rem;
        border-left: 2px solid $white;
        border-bottom: 2px solid $white;
        transform: rotateZ(-45deg);
      }

      &:hover {
        color: $white;

        .nav-list__item::after {
          transform: rotateZ(135deg);
        }

        .sub-nav {
          display: block;
        }
      }
    }

    &:hover {
      color: $white;
    }
  }

  &__item {
    position: relative;
    display: block;
    padding: 1rem 1.2rem;
    color: inherit;
    text-decoration: none;
    font-size: .875rem;
    letter-spacing: .05rem;
    cursor: pointer;
  }

  @media screen and (min-width: $desktop) {
    display: flex;
    align-items: flex-start;
    padding: 3rem 0;

    > li {
      opacity: 0;
      margin: 0 .3rem;
      padding: .7rem 0;

      &.has-sub-nav {
        border-radius: 1rem;
        background-color: $white;
        position: relative;
        overflow: hidden;

        .nav-list__item::after {
          position: static;
          border-left: 2px solid $blue;
          border-bottom: 2px solid $blue;
        }

        &:hover {
          color: $blue;
          box-shadow: 0 0 40px 0 rgba(0, 28, 85, .08);
        }
      }

      &:hover {
        color: $blue;
      }
    }

    &__item {
      display: inline-block;
      padding: .5rem 1.2rem;
    }
  }
}

.nav-toggler {
  position: relative;
  width: 3rem;
  height: 2.2rem;
  border: none;
  border-radius: 1.1rem;
  background-color: $white;
  box-shadow: 0 0 40px 0 rgba(0, 28, 85, .15);
  cursor: pointer;

  &:focus {
    outline: none;
  }

  &::-moz-focus-inner {
    border: 0;
  }

  &__line {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -.09rem;
    margin-left: -.2rem;
    display: inline-block;
    height: .1875rem;
    width: 1rem;
    background-color: $acccent;
    border-radius: .09rem;

    &::before,
    &::after {
      content: '';
      position: absolute;
      height: .1875rem;
      background-color: $acccent;
      border-radius: .09rem;
    }

    &::after {
      top: -.4rem;
      left: 50%;
      width: .9rem;
      margin-left: -.95rem;
    }

    &::before {
      bottom: -.45rem;
      left: 50%;
      width: 1.2rem;
      margin-left: -.9rem;
    }
  }

  &.active .nav-toggler__line {
    background-color: transparent;

    &::after,
    &::before {
      width: 1.4rem;
      margin-left: -1rem;
    }

    &::after {
      top: 0;
      transform: rotateZ(-45deg);
    }

    &::before {
      bottom: 0;
      transform: rotateZ(45deg);
    }
  }
}

.header {
  display: flex;
  align-items: center;
  width: 100%;
  background-color: $white;

  &__logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 5rem;
    text-decoration: none;
    transition: opacity .2s;

    .text {
      margin-top: .3rem;
      max-width: 3.2rem;
    }

    &:hover {
      opacity: .8;
    }
  }

  &-action-btn {
    margin-left: 2rem;
    margin-top: 2.8rem;
  }

  &.floating {
    height: 5.5rem;

    .header__logo {
      flex-direction: row;
    }

    .nav-list {
      padding: 1rem 0;
    }

    .header-action-btn {
      margin-top: .8rem;
    }
  }

  @media screen and (max-width: $desktop - 1) {
    height: 4rem;

    &.floating {
      height: 4rem;
    }

    &__logo {
      flex-direction: row;
      width: 3.5rem;

      .text {
        max-width: 2.4rem;
      }
    }

    .nav-toggler {
      margin-left: auto;
      position: relative;
      z-index: 20;
    }

    &__nav {
      position: fixed;
      top: 0;
      bottom: 0;
      right: 0;
      width: 18.75rem;
      padding-top: 4rem;
      background-color: $acccent;
      z-index: 10;
      transform: translateX(100%);
      transition: transform .3s;

      &::before {
        content: '';
        width: 0;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, .01);
        transition: background-color .3s;
      }

      &.visible {
        transform: translateX(0);

        &::before {
          width: 70rem;
          left: -70rem;
          background-color: rgba(0, 0, 0, .3);
        }
      }

      .header-action-btn {
        margin-left: 0;
        padding: 0 1rem;
      }

      .btn {
        width: 100%;
      }
    }
  }

  @media screen and (min-width: $desktop) {
    height: 9.5rem;

    .nav-toggler {
      display: none;
    }

    &__nav {
      display: flex;
      align-self: flex-start;
      align-items: flex-start;
      margin-left: auto;
    }
  }
}

.promo {
  position: relative;
  padding-top: 4rem;

  &__title {
    max-width: 23rem;
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 2.2rem;
    font-family: $title-font;
    line-height: 1.3;
    font-weight: bold;
    letter-spacing: .05rem;
  }

  &__text {
    max-width: 25rem;
    margin-top: 0;
    margin-bottom: 3.5rem;
    font-size: 1em;
    line-height: 1.5;
  }

  &__image {
    display: none;
  }

  @media screen and (max-width: $mobile - 1) {
    .btn {
      width: 100%;
    }
  }

  @media screen and (min-width: $tablet_s) {
    padding-bottom: calc(2vw + 2rem);

    &__title {
      padding-top: calc(5vw + 2rem);
    }

    &__text {
      margin-bottom: 3.5rem;
    }

    &__image-mob {
      display: none;
    }

    &__image {
      display: block;
      position: absolute;
      max-width: 50vw;
      top: 4rem;
      right: -4vw;
      z-index: -1;
    }
  }

  @media screen and (min-width: $desktop) {
    &__title {
      max-width: 28rem;
      margin-bottom: 3.5rem;
      padding-top: 10rem;
      font-size: 3.5rem;
    }
  }
}

.banner {
  position: relative;
  padding: 3.5rem;
  background-color: $blue;
  text-align: center;
  background-repeat: no-repeat;
  overflow: hidden;

  &__title {
    max-width: 22rem;
    margin: 0 auto 2rem;
    color: $white;
    font-weight: bold;
    font-size: 2.25rem;
    font-family: $title-font;

    .light {
      font-weight: 400;
    }
  }

  @media screen and (max-width: $tablet - 1) {
    padding: 2rem;

    &__title {
      font-size: 1.8rem;
    }
  }

  @media screen and (max-width: $mobile - 1) {
    border-radius: 1.5rem;
    background-image: url('../images/banner_r.jpg');
    background-position: 100% 0;
    background-size: 10rem;

    &__title {
      max-width: 19rem;
      margin: 5rem 0 2rem;
      text-align: left;
    }

    .btn {
      width: 100%;
    }
  }

  @media screen and (min-width: $mobile) {
    background-image: url('../images/banner_l.jpg'), url('../images/banner_r.jpg');
    background-position: 0 50%, 100% 0;
    background-size: contain;
    border-radius: 3rem;
  }
}

.gs-header {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 3;

  &.full-height {
    top: 0;
    bottom: 0;
  }

  @media screen and (min-width: $desktop) {
    background-color: $white;
  }
}
