.section {
  padding-top: 6vw;
  padding-bottom: 6vw;

  &__title {
    padding-top: 2rem;
    margin-top: 0;
    margin-bottom: 1.2rem;
    font-size: 1.8rem;
    font-family: $title-font;

    &--l {
      font-size: 2.2rem;
    }

    &--accent {
      color: $acccent;
    }
  }

  &__description {
    max-width: 20.25rem;
    font-size: 1rem;
    font-weight: normal;
    opacity: .35;
  }

  &--bg-g {
    background-color: $gray;
  }

  &--bg-wawe-b {
    background-image: url('../images/wawe_fff.svg');
    background-position: 100% 100%;
    background-size: 100% 4rem;
    background-repeat: no-repeat;
  }

  &--bg-g-bottom {
    background-image: linear-gradient(to bottom, $white 50%, $gray 50%);
  }

  &--bg-g-top {
    background-image: linear-gradient(to top, $white 50%, $gray 50%);
  }

  @media screen and (min-width: $tablet) {
    &__title {
      margin-bottom: 3rem;
      font-size: 2.5rem;
      text-align: center;

      &--l {
        font-size: 3.5rem;
      }
    }

    &__description {
      margin-left: auto;
      margin-right: auto;
      text-align: center;
    }

    &--bg-wawe-b {
      background-size: 100% 10rem;
    }
  }

  @media screen and (min-width: $desktop_l) {
    padding-top: 3vw;
    padding-bottom: 3vw;
  }
}

.section-exchange {
  padding-bottom: 10vw;
}

.text-block {
  max-width: 24rem;

  &__title {
    margin-top: 1.5rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
    font-family: $title-font;
  }

  &__description {
    font-size: 1rem;
    opacity: .35;
  }

  &__content {
    margin-top: 1.4rem;
    margin-bottom: 1.4rem;
    font-size: 1rem;
    line-height: 1.5;
  }

  &--fluid {
    max-width: 100%;
  }

  &--s {
    max-width: 20.5rem;
  }

  &--b-bottom {
    border-bottom: 1px solid $gray_1;
  }

  @media screen and (max-width: $tablet_s - 1) {
    max-width: 100%;
  }
}

.workspace {
  display: flex;
  flex-direction: column-reverse;
  padding-top: 5vw;

  &__img-wrapper {
    position: relative;
    flex-grow: 1;
    text-align: center;

    > img {
      display: inline-block;
    }
  }

  .mob-button {
    text-align: center;
  }

  @media screen and (max-width: $mobile - 1) {
    .mob-button {
      .btn {
        width: 100%;
      }
    }
  }

  @media screen and (max-width: $tablet_s - 1) {
    .dt-button {
      display: none;
    }
  }

  @media screen and (min-width: $tablet_s) {
    flex-direction: row;

    .mob-button {
      display: none;
    }
    &__img-wrapper {
      max-width: 65%;
    }
  }
}

.videoplayer {
  display: none;
  position: absolute;
  top: 55px;
  left: 29%;
  width: 42%;
  transform: skewX(3deg) rotateZ(-7deg);

  video {
    max-width: 100%;
  }

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    font-size: 0;
    width: 4rem;
    height: 4rem;
    transform: translate(-2rem, -2rem);
    background-color: $blue;
    border: none;
    border-radius: 50%;
    z-index: 2;
  }
}

.indicators-group {
  padding-top: 5vw;

  &__img-wrapper {
    position: relative;
    flex-basis: 55%;
    padding-bottom: 55%;
    margin-left: auto;
    background-color: $gray_1;
    overflow: hidden;
    border-radius: 50%;
    transform: scale(0);
    opacity: 0;

    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 75%;
      opacity: 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: calc(7vw + 2rem);
    padding-top: 3rem;

    .mob-img {
      flex-basis: 18rem;
      min-width: 18rem;
      padding-bottom: 18rem;
      margin-left: auto;
      margin-right: auto;
      border-radius: 50%;
      background-size: cover;
      background-position: 50% 50%;
      box-shadow: 0 0 25px -2px rgba(0, 0, 0, .1);
    }
  }

  @media screen and (max-width: $desktop - 1) {
    &__img-wrapper {
      display: none;
    }
  }

  @media screen and (min-width: $mobile) {
    &__item {
      .mob-img {
        flex-basis: 40%;
        min-width: 40%;
        padding-bottom: 40%;
      }
    }
  }

  @media screen and (min-width: $tablet_s) {
    &__item {
      align-items: flex-start;
    }

    &__item:not(.reversed) {
      flex-direction: row;
    }

    &__item.reversed {
      flex-direction: row-reverse;
    }
  }
}

.tile {
  padding: 5rem 0;

  &__row {
    display: flex;
    list-style: none;
  }

  @media screen and (max-width: $tablet - 1) {
    margin-bottom: 2rem;
    padding-top: 3vw;
    padding-bottom: 0;
    overflow-x: auto;

    &-wrap {
      display: flex;
      flex-direction: column-reverse;
    }
  }
}

.tile-item {
  position: relative;
  min-width: 9.56rem;
  flex-basis: 9.56rem;
  margin: 0 .5rem;
  padding: 1rem;
  background-color: $white;
  border-radius: .4rem;
  font-size: .875rem;

  &--small {
    display: flex;
    min-width: 8.34rem;
    min-height: 3.125rem;
    padding: 0;
    overflow: hidden;

    .tile-item__title {
      display: inline-flex;
      padding: .5rem;
      align-items: center;
    }
  }

  &__icon {
    background-repeat: no-repeat;
    background-size: 1.2rem;
    background-position: 50% 50%;

    &--top {
      display: block;
      position: absolute;
      top: 0;
      right: 1rem;
      width: 2.7rem;
      height: 2.7rem;
      border-radius: 50%;
      background-color: red;
      transform: translateY(-50%);
    }

    &--left {
      display: inline-block;
      width: 3.125rem;
      height: 100%;
    }

    &--orange {
      background-color: $orange;
    }

    &--green {
      background-color: $green;
    }
  }

  &__title {
    margin: 0;
    opacity: .4;
    text-transform: uppercase;
  }

  &__value {
    max-width: 65%;
    font-weight: bold;
    letter-spacing: .07rem;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__score {
    max-width: 35%;
    overflow: hidden;
    text-overflow: ellipsis;

    &.positive {
      color: $green;
    }

    &.negative {
      color: $red;
    }
  }
}
